<template>
    <div style="width:100%;display: flex;justify-content: center;">
        <div style="width:80%;padding: 20px;">
            <div style="width:100%;color:black;border:2px solid #DCDCDC;border-radius: 4px;">
                <el-row>
                    <el-col :span="16">
                        <div style="padding:20px;width:100%;height:100%;">
                           <el-form ref="formup"   label-width="80px">
                                <el-row :gutter="20">
                                    <el-col :span="12">
                                        <el-form-item label="成果名称:">
                                            {{ resultInfo.resultName }}
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="12">
                                        <el-form-item label="成果级别:" >
                                            {{ resultInfo.resultDetails.level }}
                                        </el-form-item>
                                    </el-col>
                                </el-row>
                                <el-row :gutter="20">
                                    <el-col :span="12">
                                        <el-form-item label="成果类型:">
                                            {{ resultInfo.resultDetails.type }}
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="12">
                                        <el-form-item label="数据格式:" >
                                            {{ resultInfo.contextType }}
                                        </el-form-item>
                                    </el-col>
                                </el-row>
                                <el-form-item label="所属项目:" v-show="resultInfo.resultDetails.level === '项目级' || resultInfo.resultDetails.level === '工点级'">
                                    {{ resultInfo.resultDetails.demoFormName }}
                                </el-form-item>
                                <el-form-item label="所属工点:" v-show="resultInfo.resultDetails.level ==='工点级' ">
                                    {{ resultInfo.resultDetails.dotFormName }}
                                </el-form-item>
                                <!-- label-width="100px" -->
                                <el-row :gutter="20" >
                                    <el-col :span="12">
                                        <el-form-item label="勘察阶断:" v-show="resultInfo.resultDetails.level === '项目级' || resultInfo.resultDetails.level === '工点级'">
                                            {{ resultInfo.resultDetails.exploreStage }}
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="12">
                                        <el-form-item label="线路方案:" v-show="resultInfo.resultDetails.level === '项目级' || resultInfo.resultDetails.level === '工点级'">
                                            {{ resultInfo.resultDetails.linePlan }}
                                        </el-form-item>
                                    </el-col>
                                </el-row>
                                <el-form-item label="成果描述:" >
                                    {{ resultInfo.resultRemark }}
                                </el-form-item>
                                <el-form-item label="成果附件:" >
                                    <ul>
                                        <li v-for="item in resultFiles">
                                            <el-link  :key="item.annexName"  type="primary" style="margin-right:10px">{{item.annexName}}</el-link>
                                            <el-button type="mini" @click="handleClickResult(item)">下载</el-button>
                                        </li>
                                    </ul>
                                    <!-- <el-link v-for="item in resultFiles" :key="item.annexName"  type="primary" style="margin-right:10px" @click="handleClickResult(item)">{{item.annexName}}</el-link> -->
                                </el-form-item>
                                <el-form-item label="校审附件:" v-show="resultInfo.resultDetails.level === '项目级' || resultInfo.resultDetails.level === '工点级'">
                                    <ul>
                                        <li v-for="item in checkFiles">
                                            <el-link :key="item.annexName"  type="primary" style="margin-right:10px">{{item.annexName}}</el-link>
                                            <el-button type="mini" @click="handleClickCheck(item)">下载</el-button>
                                        </li>
                                    </ul>
                                    <!-- <el-link v-for="item in checkFiles" :key="item.annexName"  type="primary" style="margin-right:10px" @click="handleClickCheck(item)">{{item.annexName}}</el-link> -->
                                </el-form-item>
                            </el-form>
                        </div>
                    </el-col>
                    <el-col :span="8">
                        <div style="width:100%;height:430px;padding: 10px;color:black">
                            <div v-if="showImgs[0]">
                                <el-carousel :interval="3000" arrow="always" style="border:2px solid #DCDCDC;">
                                    <el-carousel-item v-for="item in showImgs" :key="item.img">
                                        <div class="image-container" @click="showPreview">
                                            <img :src="item" style="max-width: 100%; max-height: 100%;object-fit: contain;" />
                                        </div>
                                    </el-carousel-item>
                                </el-carousel>
                            </div>
                            <div v-else @click="showPreview" style="border:1px solid black">
                                <img :src="imgurl" style="width: 100%; height: 100%;object-fit: contain;">
                            </div>
                            <!-- <img :src="img" style="width:100%;height:360px;border:1px solid gray" alt=""> -->
                            <div style="width:100%;display:flex;justify-content: center;padding:15px">
                                <div style="flex:1;text-align: center;font-size:18px;">成果缩略图</div>
                                <!-- <div style="flex:1;text-align: center;">
                                    <el-link type="primary" style="font-size:16px" @click="showPreview">预览</el-link>
                                </div> -->
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </div>
            <el-dialog :modal-append-to-body=false  :visible.sync="previewVisible">
                <!-- <el-image :src="this.img" style="width: 100%;max-width: 300px;margin:0 auto" fit="contain"></el-image> -->
                <div style="width: 100%;height:450px;margin: auto;line-height: 450px;overflow: hidden;" @wheel="handleWheel">
                    <img :src="currentImage" :style="imageStyle">
                    <!-- <img v-for="(item,index) in showImgs" :src="item" :style="imageStyle" > -->
                </div>
                <el-button @click="nextImage">下一张</el-button>
            </el-dialog>
            <div style="width:100%;color:black;border:2px solid #DCDCDC;margin-top:20px;padding: 30px 40px;border-radius: 4px;">
              <el-form :rules="rules">
                <el-row>
                    <el-col :span="12">
                    <el-form-item label="审核意见:" prop="advice">
                        
                        <template>
                            <div class="radio-group">
                                <el-radio-group v-model="status" :disabled="disabled">
                                    <div class="radio-item">
                                        <el-radio :label = true>审核通过</el-radio>
                                    </div>
                                    <div class="radio-item">
                                        <el-radio :label= false>审核不通过</el-radio>
                                    </div>
                                </el-radio-group>
                            </div>
                        </template>
                    </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="意见说明:">
                            <el-input type="textarea" v-model="advicetext" :rows="4" style="width:80%" :disabled="disabled"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
              </el-form>
            </div>
            <div class="submitInput" style="padding:20px 50px">
                <el-row>
                    <el-col :span="24">
                        <el-button type="danger" style="float: left;margin-right: 20px;" @click="saveCheckStatus()">保存</el-button>
                        <el-button type="info" style="float: left;" @click="$router.back(-1)">取消</el-button>
                    </el-col>
                </el-row>
            </div>
        </div>
    </div>
</template>
<script>
import myaxios from '../../utils/httpRequest'
import qs from 'qs'
export default {
   data() {
      return {
        disabled:false,  //如果审核通过就禁用
        zoomLevel: 1,  //预览图缩放
        id:0,
        currentIndex:0,
        showImgs:[], //预览图url组
        imgurl: require('../../assets/images/null.png'), //预览图
        previewVisible: false, // 预览对话框的显示状态
        status: null, // 选中的审核状态
        advicetext:'', //成果描述
        isLoading: false,
        resultInfo: {
            resultDetails:{},
            annex:[],
        },
        //相关存储
        resultFiles:[],  //type-1
        checkFiles:[],   //type-2
        resultImg:[],   //type-3
        rules: {
            advice: [
                { required: true, message: '', trigger: 'blur'}
            ],
        },
      }
   },
   created(){
   },
   mounted(){
    this.getCheckMsg();
   },
   computed: {
        currentImage() {
            return this.showImgs[this.currentIndex];
        },
        imageStyle() {
            return {
                transform: `scale(${this.zoomLevel})`,
                transformOrigin: 'center',
                objectFit: 'contain',
                height: '100%',
                width: '100%',
            };
        },
        //无图片时显示默认null.png
        getImageSrc(item) {
            return item ? item : this.imgurl;
        }
        // checkStatus(){
        //     if(this.resultInfo.resultDetails.checkHistory){
        //         if(this.resultInfo.resultDetails.checkHistory[0]){
        //             return this.resultInfo.resultDetails.checkHistory[this.resultInfo.resultDetails.checkHistory.length - 1].status
        //         }    
        //     } else {
        //         return null
        //     }
        // }
    },
   methods:{
    //控制预览的下一张
    nextImage() {
      this.currentIndex = (this.currentIndex + 1) % this.showImgs.length;
    },
    //滑轮放大缩小图片
    handleWheel(event) {
        event.preventDefault();
        const zoomDelta = event.deltaY > 0 ? -0.1 : 0.1;
        this.zoomLevel += zoomDelta;
        // 设置缩放范围
        this.zoomLevel = Math.max(0.1, this.zoomLevel);
    },
    async getCheckMsg() {
        try {
            this.id = this.$route.params.id;
            const res = await myaxios.get(`/getGeoResult/${this.id}`);
            this.resultInfo = res.data.data;
            console.log("全部详细信息",this.resultInfo);
            if(this.resultInfo.resultDetails.checkHistory){
                if(this.resultInfo.resultDetails.checkHistory[0]){
                    this.status = this.resultInfo.resultDetails.checkHistory[this.resultInfo.resultDetails.checkHistory.length - 1].status;
                    this.advicetext = this.resultInfo.resultDetails.checkHistory[this.resultInfo.resultDetails.checkHistory.length - 1].message
                    if(this.status === true){
                        this.disabled = true;
                    }
                }    
            } else {
                this.status = null
            }
            let annex = res.data.data.annex;
            console.log(annex);
            for(let i = 0; i < annex.length; i++){
                if(annex[i].annexType === "3"){
                    this.resultImg.push(annex[i]);
                }
                if(annex[i].annexType === "2"){
                    this.checkFiles.push(annex[i]);
                }
                if(annex[i].annexType === "1"){
                    this.resultFiles.push(annex[i]);
                }
            }
            console.log("3",this.resultImg,"2",this.checkFiles,"1",this.resultFiles);
            let imgfiles = [];
            for(let i = 0; i<this.resultImg.length; i++){
                imgfiles[i] = await myaxios.get(`/getAnnexFile/${this.resultImg[i].resultId}/${this.resultImg[i].fileId}`,{responseType: 'blob'});
            };
            console.log("imgfiles",imgfiles)
            //将缩略图转化为baes64   
            for(let i = 0; i<imgfiles.length; i++){
                const reader = new FileReader();
                //如果你使用普通的函数声明 function(e) {...}，在函数作用域内部，this 的值将不再指向组件实例，而是指向函数本身。这可能会导致 this.img 无法正确访问和更新，因为 this 不再指向组件实例。
                reader.onload = (e) =>{
                    const base64Data = e.target.result;
                    this.showImgs.push(base64Data);
                    // this.showImgs[i] = base64Data
                };
                reader.readAsDataURL(imgfiles[i].data);
            };      
            console.log("this.showImgs",this.showImgs)
        } catch (err) {
            console.log(err);
        }
    },
    showPreview() {
      // 设置预览图片的 URL
      // 显示预览对话框
      this.previewVisible = true;
    },
    //下载
    async handleClickResult(item){
        let res = await myaxios.get(`/getAnnexFile/${item.resultId}/${item.fileId}`);
        console.log(res.config.url);
        window.open(res.config.url, "_blank");
    },
    async handleClickCheck(item){
        let res = await myaxios.get(`/getAnnexFile/${item.resultId}/${item.fileId}`);
        console.log(res.config.url);
        window.open(res.config.url, "_blank");
    },
    //审核保存
    async saveCheckStatus() {
        try {
            const params = {
                message: this.advicetext,
                resultId: this.id,
                status: this.status , //this.status === '1'? true : false
            };
            const formData = qs.stringify(params);
            const headers = {'Content-Type': 'application/x-www-form-urlencoded',};
            let res = await myaxios.post(`/checkGeoResult`,formData, { headers } );
            console.log(res);
            if(res.data.code === 200) {
                this.$message({
                    type:'success',
                    message:'审核完成'
                })
                this.$router.back(-1)
            }
            if(res.data.code === 500) {
                this.$message({
                    type:'warning',
                    message:`${res.data.data}`
                })
            }
        } catch (error) {
            console.error(error);
        }
    },
   },
}
</script>
<style lang="scss" scoped>
// 单选框竖向排列
.radio-group {
  display: flex;
  flex-direction: column;
//   margin-top:30px
}
.radio-item {
    margin-top:10px;
}
.image-container {
  height: 100%;
  width: 100%;
  /* border:1px solid black; */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
//改轮播
:deep(.el-carousel__button) {
  /* border: 1px solid red; */
  width: 0.5625rem;
  height: 0.5625rem;
  border-radius: 50%;
  background-color:gray;
}
</style>
